.container{
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  color: white;
}
.title{
  position: absolute;
  top: 20px;
  left: 30px;
  color: red;
  font-size: 44px;
  font-weight: 900;
}
.box{
  padding-top: 60px;
}
.today{
  color: white;
  font-size: 28px;
}
.movies{
  display: flex;
  margin-top: 20px;
  width: 1300px;
  overflow: scroll;
}
.movies::-webkit-scrollbar{
  display: none;
}

